import cps_image from '../../assets/images/work/cps_example.jpg'
import representation_mapping from '../../assets/images/work/representation_mapping.svg'
import approach from '../../assets/images/work/approach.png'
import significance_level_fig from '../../assets/images/work/significance_level_figure.png'
import compute_epsilon from '../../assets/images/work/compute_epsilon.png'
